import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-rbac-roles/@core/permissions/permission.store';
import { DataStoreService } from './pics-rbac-roles/@core/service/data-store.service';
import { RBACINFO } from './pics-rbac-roles/@core/urls/rbac-url.config';

@Component({
  selector: 'rbac-roles',
  template: `
      <roles></roles>
  `,
  styles: [
  ]
})
export class RbacRolesComponent implements OnInit {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public roleEvent!: Observable<any>;
  @Input() public INPUTVALIDATIONMETHOD? : any;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.roleEvent.subscribe(val => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('HTTPSERVICE',val.httpService);
      this._storeservice.setData('INPUTVALIDATIONMETHOD', this.INPUTVALIDATIONMETHOD);
    })
  }
}
